body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
  scroll-behavior: smooth; }

span {
  margin-left: 10px; }

.App {
  text-align: center;
  font-family: "Courier New", Courier, monospace; }

.link {
  border: 2px solid black;
  color: white;
  padding: 10px;
  text-align: center;
  display: inline-block;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  margin: 0 5px; }
  .link:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1); }

.header {
  overflow: hidden;
  text-align: right;
  padding: 20px 10px; }

.header a {
  color: black;
  padding: 12px;
  text-decoration: none;
  font-size: 18px;
  line-height: 25px;
  border-radius: 4px; }

.particles {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 400vh;
  z-index: -1;
  background-color: white; }
  .particles.background {
    z-index: 0; }

.page {
  height: 100vh; }
  .page.subpage {
    text-align: left;
    padding-left: 30px;
    height: 120vh; }

.title {
  font-size: 70px;
  padding-top: 35vh; }

.headerTabs {
  background: transparent;
  border: none !important;
  cursor: pointer;
  font-size: 20px;
  padding: 0 15px; }
  .headerTabs:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1); }

.leftCol {
  float: left;
  width: 55%; }
  .leftCol.about {
    font-size: 18px; }

.rightCol {
  float: right;
  width: 33%;
  margin-top: 3%;
  margin-right: 3%;
  background-color: white; }
  .rightCol.background {
    border: 1px inset lightgray;
    padding: 10px;
    border-radius: 10px; }

.axis {
  font-weight: bolder;
  position: relative;
  left: 150px;
  bottom: 25px; }

.sprite {
  position: relative;
  left: 10px;
  bottom: 5px; }
  .sprite.hideBottom {
    -webkit-clip-path: inset(0px 0px 7px 0px);
            clip-path: inset(0px 0px 7px 0px);
    margin-bottom: -5px; }

